  #t_1_14.h4 Llamados a la accin

  p.mb-2 Es un bloque de texto que destaca por su composicin y diseo. Su principal funcin es incentivar al usuario a realizar una accin a travs de un botn o hipervnculo, en este caso, con el fin de poder acceder a recursos como infografas, videos, documentos y sitios web externos e independientes del componente formativo.

  ul.lista-ul.mb-3
    li 
      i.lista-ul__vineta
      | Abrir PDF
    li 
      i.lista-ul__vineta
      | Infografas
    li 
      i.lista-ul__vineta
      | Documentos
    li 
      i.lista-ul__vineta
      | Videos externos 
    li 
      i.lista-ul__vineta
      | Enlaces

  .h5 Requerimientos de informacin:

  ul.lista-ul.mb-5
    li 
      i.lista-ul__vineta
      | Ttulo llamativo, claro y conciso. 
    li 
      i.lista-ul__vineta
      | La redaccin de los textos deben crear la necesidad de consultar el contenido sugerido (anexos).
    li 
      i.lista-ul__vineta
      | Se sugiere un prrafo de acompaamiento que no sobrepase las 3 lneas de texto en su extensin, cuya caracterstica principal sea incentivar al aprendiz a consultar el recurso propuesto.

  .h5 Ejemplo: Llamado a la accin 1 Accin de infografas
  .tarjeta.tarjeta--amarilla.p-4.p-md-5.mb-5
    .row.justify-content-around.align-items-center
      .col-8.col-sm-6.col-md-4.mb-4.mb-md-0
        img(src="@/assets/componentes/ej-01.png")

      .col-md.col-lg-6
        .h2 Ttulo del recurso
        p.mb-4 There is no right answer. The splendor of the mystery is that you dont understand. Your rapidograph pens are dried up, the x-acto blades in your bag are rusty, and your mind is dull. Stop clicking your mouse, get messy, go back to the basics and make something original. Why are you reading all of this?
        
        a.boton.boton--b(:href="obtenerLink('/downloads/prueba.pdf')" target="_blank" type="application/pdf")
          span Descargar
          i.fas.fa-file-download

  .h5 Ejemplo: Llamado a la accin 2 Recursos externos
  .tarjeta.tarjeta--morada.p-3.mb-5
    .row.justify-content-around.align-items-center
      .col-3.col-sm-2.col-lg-1
