          .bloque-texto-b__texto.h4.mb-0 La Seguridad y Salud en el Trabajo -SST- es la disciplina que trata la prevencin de las lesiones y enfermedades causadas por las condiciones de trabajo, y de la proteccin y promocin de la salud de los trabajadores

