    p.mb-4 Segn la Ley 1562 de 2012  el Sistema de Gestin de Seguridad y Salud en el Trabajo SG-SST busca con la participacin de varias disciplinas y con el compromiso activo de todos los niveles de la organizacin optimizar las condiciones de trabajo y de salud de la poblacin trabajadora, mediante acciones coordinadas de promocin de estilos de trabajo y vida saludables, la prevencin y el control de los factores de riesgos, de manera que faciliten el bienestar de la comunidad laboral y la eficiencia del Ministerio.

    p.mb-4 As como tambin el SG-SST busca garantizar que en el lugar de trabajo todos y cada uno de los operarios tengan el conocimiento necesario para realizar las labores, adems que cuenten con el equipo adecuado para que haya una prctica segura y que, en el imprevisto de algn accidente, se cuente con lo mnimo para ser atendidos oportunamente, desde la capacitacin como contar con los elementos necesarios. 

    .row.mb-5.justify-content-center
