        p El sistema de gestin aplica a todos los empleadores pblicos y privados, los trabajadores dependientes e independientes, los trabajadores cooperados, los trabajadores en misin, los contratantes de personal bajo modalidad de contrato civil, comercial o administrativo, las organizaciones de economa solidaria y del sector cooperativo, las empresas de servicios temporales, las agremiaciones u asociaciones que afilian trabajadores independientes (Ministerio del Trabajo, 2020).

