    p.mb-4 Dentro del marco normativo del Sistema de Gestin de Seguridad y Salud en el Trabajo se encuentran leyes, decretos y resoluciones que orientan en los diferentes procesos sobre la salud y el bienestar de los trabajadores, considerando que la responsabilidad de adoptar las medidas para garantizar la salud ocupacional de las personas trabajadoras en el lugar de trabajo corresponde a cada unidad productiva del sector agrcola.

