    #t_1_2.titulo-segundo
      .h4 1.2 Campo de aplicacin

    .row.mb-4.justify-content-center
      .col-6
        p.mb-4 El SG-SST debe contener y manifestar, de forma clara, cmo la produccin agrcola o la unidad productiva se compromete a minimizar los riesgos de accidentes que se identifiquen en las tareas o actividades llevadas a cabo, tanto con los trabajadores como operarios temporales. En este, se debe tener en cuenta el personal, el factor de riesgo, la descripcin del factor de riesgo, posibles medidas de control, los daos y su severidad del dao, probabilidades de ocurrencia, as como tambin categorizar los riesgos y contar con las medidas de control para los riesgos identificados a travs del diagnstico realizado.      
      .col-6
        figure
