    p.mb-4 El SG-SST debe incluir un plan de capacitacin para los trabajadores y operarios en el que se incluyan temas como el de la sealizacin de las reas, equipos de proteccin personal, extintores, botiqun, instructivos de emergencia y primeros auxilios, manejo seguro de plaguicidas, accidentes entre otros, buscando, as mismo, su bienestar y por ende, se tienen en cuenta espacios como comedores, bateras sanitarias, lavamanos, disponibilidad de agua para beber; lo cual estar a cargo de la persona de salud, seguridad y bienestar del trabajador encargado de hacer un seguimiento.

