    p.mb-5 De acuerdo con las BPA la o las personas capacitadas en primeros auxilios (mnimo una, en la produccin agrcola o finca) debe de permanecer en esta en el momento de llevar a cabo las labores para atender una posible emergencia. Adems, en la implementacin de BPA se encuentra la inocuidad de los productos como pilar fundamental, por ello es indispensable contar con la informacin bsica sobre la higiene en la que se incluye: comer y beber en reas designadas, mantener las manos limpias, usar los trajes o equipos de proteccin personal de acuerdo con la labor, y avisar cualquier problema de salud o infeccin que se pueda presentar. 

