    #t_1_5.titulo-segundo
      .h4 1.5 Riesgos asociados a la produccin agrcola
    
    p.mb-5 Buena parte del trabajo agrcola es, por naturaleza, fsicamente exigente. El riesgo de accidentes aumenta con el cansancio, la utilizacin de herramientas poco ergonmicas, las dificultades del terreno, la exposicin a condiciones climticas extremas y la mala salud general que se asocia a trabajar y vivir en comunidades rurales remotas. Estos problemas se ven agravados por el hecho de que las condiciones de trabajo y de vida terminan solapndose.  En trminos de fallecimientos, lesiones y problemas de salud relacionados con el trabajo, el sector agrcola es uno de los tres sectores de actividad ms peligrosos junto con la construccin y la minera (OIT, 2015).

