            .h6 Riesgo fsico
            p Estn relacionados factores ambientales que actan sobre los tejidos y rganos del cuerpo de la persona trabajadora, que pueden producir efectos nocivos, de acuerdo con la intensidad y tiempo de exposicin. Entre las labores se encuentran: preparacin del terreno, siembra y resiembra, mantenimiento del cultivo, cosecha, manejo integrado de plagas, postcosecha.
