              .h6 Riesgo biolgico
              p Son agentes orgnicos como los hongos, virus, bacterias, parsitos, insectos presentes en determinados ambientes laborales, que pueden desencadenar enfermedades infectocontagiosas, reacciones alrgicas o intoxicaciones al ingresar al organismo. Entre las labores se encuentran: preparacin del terreno, siembra y resiembra, mantenimiento del cultivo, cosecha, manejo integrado de plagas, postcosecha.
      .col-lg-10
        .tarjeta-Avatar-b.mb-5
