            .tarjeta.tarjeta--azul.p-4
              .h6 Riesgo qumico
              p Toda sustancia orgnica, inorgnica, natural o sinttica que durante la fabricacin, manejo, transporte, almacenamiento y uso pueda incorporarse al aire ambiente y ser inhalada; entrar en contacto con la piel o ser ingerida, con efectos irritantes, corrosivos, asfixiantes o txicos y en cantidades o tiempos de exposicin que tengan probabilidades de lesionar la salud de las personas.  Entre las labores se encuentran: manejo integrado de plagas, enfermedades y arvenses de los cultivos, cosecha y postcosecha.      
      .col-lg-10
        .tarjeta-Avatar-b.mb-5
