            .tarjeta.tarjeta--azul.p-4
              .h6 Riesgo locativo
              p Cuando se trata de construccin, mantenimiento, topografa del terreno y deterioro de instalaciones locativas. Entre las labores se encuentran: preparacin del terreno y postcosecha.
      .col-lg-10
        .tarjeta-Avatar-b.mb-5
            img.minWC(src='@/assets/curso/tema1/risk05.svg' alt='AvatarTop')
            .tarjeta.tarjeta--azul.p-4
              .h6 Riesgo mecnico
              p Factores laborales por la accin mecnica de mquinas, equipos, herramientas o materiales proyectados slidos o fluidos que pueden conllevar a producir un accidente. Entre las labores se encuentran: preparacin del terreno, siembra, resiembra, aplicacin de plaguicidas, cosecha, postcosecha.
      .col-lg-10
        .tarjeta-Avatar-b.mb-5
            img.minWC(src='@/assets/curso/tema1/risk06.svg' alt='AvatarTop')
            .tarjeta.tarjeta--azul.p-4
